.custom-button {
   min-width: 165px;
   width: auto;
   height: 50px;
   letter-spacing: 0.5px;
   line-height: 50px;
   padding: 0 35px 0 35px;
   font-size: 14px;
   background-color: black;
   color: white;
   text-transform: uppercase;
   // font-family: 'Open Sans Condensed';
   font-weight: bolder;
   border: 1px solid transparent;
   cursor: pointer;
   display: flex;
   justify-content: center;
   &:hover {
     background-color: white;
     color: black;
     border: 1px solid black;
   }

   &.google-sign-in{
      background: #4285f4;
      color: white;
      min-width: fit-content;

      &:hover{
         background: #357ae8;
         
      }
   }
   &.inverted{
      background-color: white;
      color: black;
      border: 1px solid black;

      &:hover{
         background-color: black;
         color: white;
         border: 1px solid transparent;
      }
   }
   
 }